}

img {
  width: 40px;
  height: 40px;
  padding: 10px 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  list-style: none;
  display: flex;
}

.nav__list {
  margin-right: 20px;
  padding: 0;
}

.nav__list:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

